___CSS_LOADER_EXPORT___.push([module.id, `/* src/pages/LocationsPage.css */

/* You can keep or add any additional styling for the page-level container here. */
.locations-page-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 1rem;
}

.contact-header {
  text-align: center;
  margin-bottom: 2rem;
}

.small-heading {
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: rgb(37, 54, 53);
  margin-bottom: 0.5rem;
}

.main-heading {
  font-size: 2rem;
  margin: 0.5rem 0;
  color: #333;
}

.sub-text {
  font-size: 1rem;
  line-height: 1.5;
  color: #666;
  max-width: 800px;
  margin: 0.5rem auto;
}

.contact-container {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin: 2rem auto;
  gap: 1rem;
}

.contact-left {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.error-message {
  color: red;
  font-size: 0.85rem;
  margin-top: 0.2rem;
}

.submit-button {
  align-self: flex-start;
  padding: 10px 20px;
  background-color: rgb(48, 68, 66);
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.contact-submitted-message {
  text-align: center;
  margin: 2rem auto;
}
